<script>
import { onMount,onDestroy } from 'svelte';
import Slide from './slide.svelte';
export let Data;
let selected = 0;
let buffer = Data;
let interval=null;
let transed = false;

buffer[selected].active = true;

function add(){
   buffer[selected].active = false;
   selected = (selected + 1)%buffer.length;
   buffer[selected].active = true;
   transed = true;
}

function sub(){
    buffer[selected].active = false;
    selected = selected - 1;
    if(selected<0)
        selected = buffer.length - 1;
    buffer[selected].active = true;
    transed = true;
}

function automatic(){
    if(!transed)
        add();
    transed = false;
}

onMount(function(){
    interval = setInterval(automatic,10000);
})
onDestroy(function(){
    clearInterval(interval);
})
</script>
<style lang="text/postcss">.left:focus,.right:focus{outline:0}.left:hover,.right:hover{color:var(--accent-color);text-shadow:0 0 1px var(--accent-glow)}.left,.right{color:inherit;border-radius:9999px;font-size:1.5rem;font-weight:900;z-index:10}
/*# sourceMappingURL=src/components/utils/slider/slider.svelte.map */</style>

<div class="m-auto w-screen h-64 overflow-hidden relative">
    <div class='absolute inset-0 flex items-center justify-between'>
        <button aria-label="previous project" class="left" on:click={sub}>&lt;</button>
        <button aria-label="next project" class="right" on:click={add}>&gt;</button>
    </div>
    {#each buffer as slide}
        <Slide data={slide}/>
    {/each}
</div>